import Vuex from 'vuex'

export default Vuex.createStore({
  state: {
    cartlist:{
    //   productId:{
    //     id: 1,
    //     imgUrl: "//gw.alicdn.com/bao/uploaded/i1/2003774307/O1CN01YOHHAT1hgeOaENu2a_!!0-item_pic.jpg_320x320q90.jpg_.webp",
    //     desc: "家用冷水壶玻璃凉水壶耐高温扎壶冰水瓶大容量凉茶壶装凉白开水杯",
    //     tages: ['￥', '137', '375人已购买'],
    //     count:0
    //   }
    }
  },
  mutations: {
    addCartItemInfo(state,payload){
      const { productId, productInfo}=payload;
      let product=state.cartlist[productId];
       if(!product){
        product = productInfo
        product.count = 1
      }
      state.cartlist[productId]=product
      // console.log(state.cartlist);
    },
    changecount(state,number){
      const { productId}=number;
      let product=state.cartlist[productId];
      product.count = product.count + number.num
      if(product.count<0){
        product.count = 0
      }
      state.cartlist[productId]=product
    }
  },
  actions: {
  },
  modules: {
  }
})
