<template>
    <div class="cart">
        <div class="header">
            <span>购物车</span>   
        </div>
        <div class="goods">
            <div class="goods-list" v-for="item in productList" :key="item.id">
                <span class="iconfont">&#xe6c1;</span>
                <img :src="item.imgUrl" alt="">
                <div class="goods-list-name">
                    <div class="goods-list-name-text">{{item.desc}}</div>
                    <span>{{item.tages[0]}}</span>
                    <span>{{item.tages[1]}}</span>
                    <div class="goods-list-name-number">
                        <span class="num" @click="() =>{ changecount(item.id,-1)}">-</span>
                        <span class="num">{{item.count}}</span>
                        <span class="num" @click="() =>{ changecount(item.id,1)}">+</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="all">
            <span class="iconfont">&#xe6c1;</span>全选
            <div class="all-num">
                <span class="all-num-text">总计：</span>
                <span class="all-num-sum">￥{{price}}</span>
                <div class="all-num-button">
                    <span>结算{{total}}</span>
                </div>
            </div>
        </div> 
    </div>
    <Footer />
</template>

<script>
import Footer from '../home/Footer'
import {computed} from 'vue'
import { useStore } from 'vuex'
const useCartEffect = ()=>{
    const store = useStore();
    const cartlist = store.state.cartlist;

    const total = computed(()=>{
    const productList = cartlist
    // console.log(productList)
    let count = 0
    if(productList){
      for( let i in productList){
        const product = productList[i]
        count+=product.count
      }
    }
    return count
  })
    const price=computed(()=>{
        const productList = cartlist
        let price =0
        if(productList){
            for(let i in productList){
                const product = productList[i]
                price+=(product.tages[1]*product.count)
            }
        }
        return price
    })
    const productList = computed(()=>{
    const productList = cartlist || []
    return productList
  })
  const changecount=(productId, num)=>{
        store.commit('changecount', {
            productId,num
        })
    }
  return {productList,changecount,total,price}
}


export default {
  name: 'Home',
  components: { Footer},
  setup(){
      const {productList,changecount,total,price} = useCartEffect();
      return{productList,changecount,total,price}
  }
  
}
</script>

<style scoped lang='scss'>
    .cart{
        width: 100%;
        background: #F7F7F7;
        .header{
            width: 100%;
            height: 80px;
            background: #19C8AB;
            span{
                display: block;
                padding: 30px 0 0 15px;
                font-size: 18px;
                color: #fff;
            }
        }
        .goods{
            width: 100%;
            &-list{
                width: 95%;
                height: 100px;
                margin: 10px auto;
                border-radius: 10px;
                background: #fff;
                .iconfont{
                    display: block;
                    float: left;
                    line-height: 100px;
                    margin-left: 5px;
                }
                img{
                    width: 20%;
                    height: 80px;
                    margin: 5px;
                    border-radius: 10px;
                }
                &-name{
                    width: 70%;
                    float: right;
                    &-text{
                        width:100%;
                        margin: 5px 0;
                        font-size: 16px;
                        font-weight: 500;
                    }
                    span{
                        font-size: 16px;
                        font-weight: 600;
                        color:#FD540A;
                    }
                    &-number{
                        float: right;
                        margin: 0 20px;
                        font-size: 16px;
                        .num{
                            margin: 0 2px;
                            width: 20px;
                            height: 30px;
                            display: block;
                            float: left;
                            text-align: center;
                            line-height: 30px;
                            background: #F7F7F7;
                            font-size: 18px;
                            color: #000;
                        }
                    }
                }
            }
        }
        .all{
            width: 100%;
            height: 50px;
            position: fixed;
            bottom: 50px;
            background: #F7F7F7;
            line-height: 50px;
            font-weight: 600;
            font-size: 14px;
            .iconfont{
                margin-left: 10px;
            }
            &-num{
                float: right;
                &-sum{
                    margin-right: 10px;
                    color: #19C8AB;
                }
                &-button{
                    float: right;
                    width: 100px;
                    background: #19C8AB;
                    color: #fff;
                    text-align: center;
                    font-size: 16px;
                }
            }
        }
    }
</style>