<template>
  <div id="footer">
    <span class="footer-list" v-for="(item,index) in footerlist" :key="index" @click="switchToTab(item.path)">
      <span class="iconfont " v-html="item.icon"></span>
      <p>{{item.text}}</p>
    </span>
  </div>
</template>
<script>
import {useRouter} from 'vue-router'
export default {
    name:'Footer',
    setup(){
      const router= useRouter();
      const footerlist=[
        {path:'Home' , icon:"&#xe62d;",text:"首页"},
        {path:'Home' ,icon:"&#xe62f;",text:"分类"},
        {path:'Cart' ,icon:"&#xe63f;",text:"购物车"},
        {path:'My' ,icon:"&#xe78b;",text:"我的"},
      ];
      const my=()=>{
         router.push({name:'My'})
      };
      const switchToTab=(path)=>{
        // console.log(path);
        router.push({name:path})
      }
      return {footerlist,my,switchToTab}
    }
}
</script>
<style lang="scss" scoped>
  #footer{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    position: fixed;
    bottom: 0;
    background: #ffffff;
    font-size: 16px;
    .footer-list{
      text-align: center;
      .iconfont{
        font-size: 20px;
      }
    }
  }
</style>