<template>
    <div class="show">
      <router-link 
        class="show-list"
        v-for="item in showlist" 
        :key="item.id" 
        :to="`/shop/${item.id}`"
      >
          <a href="#">
            <img :src="item.imgUrl" alt="">
          </a>
          <div class="show-name">
            <span>{{item.desc}}</span>
          </div>
          <div class="show-price">
            <span 
              v-for="(innerItem,innerIndex) in item.tages" 
              :class = "{'show-price-icon':true, 'show-price-num':innerIndex===2 }"
              class="show-price-icon" 
              :key="innerIndex"
            >
              {{innerItem}}
            </span>
          </div>
      </router-link>
   </div>
</template>
<script>
import {get} from '../../untils/request'
import { ref } from 'vue'
const useShowEffect = ()=>{
  
  const showlist=ref([]);
 
  const pleaseShow=async ()=>{
    const result =await get('api/show')
    if(result.code===0){
      showlist.value=result.data.showlist
    }
  }
  return {showlist,pleaseShow}
}
export default {
  name: 'Show',
  setup(){
    const {showlist,pleaseShow}=useShowEffect();
    pleaseShow();
    return {showlist}
  }
}
</script>
<style lang="scss" scoped>
a{text-decoration: none;}
  .show{
    display: flex;
    flex-wrap: wrap;
    margin-top: 5px;
    justify-content: space-around;
    &-list{
      margin-top: 10px;
      border-radius: 10px;
      background: #fff;
      width: 45%;
      a{
        // display: block;
        img{
        border-radius: 10px 10px 0 0;
        width: 100%;
      }
      }
      .show-name{
        margin: 10px 0 5px 5px;
        font-size: 16px;
        display: -webkit-box; /* 必须结合的属性 ，将对象作为弹性伸缩盒子模型显示 */
        -webkit-box-orient: vertical; /* 必须结合的属性 ，设置或检索伸缩盒对象的子元素的排列方式 */
        -webkit-line-clamp: 2;
        overflow: hidden;
        span{
          color: #000;
        }
      }
      .show-price{
        margin: 10px 0 10px 0;
        &-icon{
          font-size: 16px;
          color: #FF5500;
        }
        &-num{
          display: block;
          font-size: 12px;
          color: #ccc;
        }
      }
    }
  }
</style>