<template>
    <div class="icon">
     <div class="icon-list" v-for="item in iconlist" :key="item.id">
       <img class="icon-list-img" :src="item.imgUrl">
       <p>{{item.name}}</p>
     </div>
   </div>
</template>
<script>
import {get} from '../../untils/request'
import{ ref} from 'vue'
const useIconEffect =()=>{
  const iconlist = ref([]);
  const pleaseIcon =async()=>{
    const result = await get('/api/icon')
     if(result.code===0){
       iconlist.value=result.data.iconlist
      }

    }
    return {iconlist,pleaseIcon}
}
export default {
    name:'Icon',
    setup(){
      const {iconlist,pleaseIcon}=useIconEffect();
      pleaseIcon();
      return {iconlist}
    }
    
}
</script>
<style lang="scss" scoped>
  .icon{
    display: flex;
    flex-wrap: wrap;
    margin-top: 5px;
    background: #fff;
    border-radius: 10px;
    &-list{
      width: 20%;
      margin-bottom: 10px;
      &-img{
        display:block;
      width: .4rem;
      height: .4rem;
      margin: 0 auto;
      }
      p{
        text-align: center;
      }
    }
  }
</style>