<template>
<div class="bdcolor">
    <div class="header">
        <img src="../../assets/login.png" alt="">
        <div class="input">
            <input type="text" class="phone" placeholder="请输入正确的手机号码" v-model="phone"/>
        </div>
        <div class="input">
            <input type="text" class="password" placeholder="请输入你的密码" v-model="password"/>
        </div>
        <div class="login" @click="pleaseLogin">登录</div>
        <div class="resgin" @click="pleaseRegister">立即注册</div>
        <Toast v-if="show" :message="toastMessage"/>
    </div>
</div>
</template>

<script>
import {useRouter} from 'vue-router'
import {post} from '../../untils/request'
import {reactive,toRefs} from 'vue'
import Toast, { useToastEffect } from '../../components/Toast'

//登录post请求
const userLoginEffect = (showToast)=>{
    const router = useRouter();
    const data = reactive({phone:'',password:''});
    const pleaseLogin = async ()=>{
        try{
            const result = await post('/api/login',{
                phone:data.phone,
                password:data.password
            })
            if(result.code ===0){
                localStorage.isLogin=true,
                router.push({name:'Home'})
            }else{
                showToast('登陆失败');
            }
        }catch(e){
            showToast('登录失败')
        }
    }
    const {phone,password}=toRefs(data);
    return{phone,password,pleaseLogin};
}

const useRegisterEffect=()=>{
    const router=useRouter();
    const pleaseRegister = () =>{
        router.push({name:'Register'});
    }
    return{pleaseRegister};
}

export default {
    name:"Login",
    components:{Toast},
    setup(){
        const { show, toastMessage, showToast } = useToastEffect()
        const { phone,password,pleaseLogin}=userLoginEffect(showToast)
        const { pleaseRegister } = useRegisterEffect()

        return{
            pleaseRegister,pleaseLogin,
            phone,password,show,toastMessage
        }
    }
}
</script>
<style scoped lang="scss">
    .header{
        position: absolute;
        top: 40%;
        left: 0;
        right: 0;
        transform: translateY(-50%);
        
        img{
            display: block;
            margin: 0 auto .4rem auto;
            width: 80px;
            height: 80px;
            border-radius: 50%;
        }
        .input{
            height: .48rem;
            margin: 0 .4rem .16rem .4rem;
            padding: 0 .16rem;
            background: #fff;
            border: 1px solid rgba(0,0,0,0.10);
            border-radius: 6px;
            border-radius: 6px;
            input{
                line-height: .48rem;
                border: none;
                outline: none;
                width: 100%;
                background: none;
                font-size: .16rem;
            }
        }
        .login{
            margin: .32rem .4rem .16rem .4rem;
            line-height: .48rem;
            background: #64E0CC;
            border-radius: .04rem;
            border-radius: .04rem;
            color: #fff;
            font-size: .16rem;
            text-align: center;
        }
        .resgin{
            text-align: center;
            font-size: .14rem;
            color: #666;
        }
    }

</style>